@import url(~photoswipe/dist/photoswipe.css);
@import url(~photoswipe/dist/default-skin/default-skin.css);
/**
* General
* ===================================================
*/
.pswp {
  z-index: 9999999;
}

.pswp .pswp__caption__center {
  text-align: center;
}

.natural-gallery {
  position: relative;
  margin-top: 2em;
  /**
    * Pictures
    * ===================================================
