}

.natural-gallery *,
.natural-gallery *::after,
.natural-gallery *::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.natural-gallery iframe {
  position: absolute;
  width: 100%;
  height: 0;
  top: 0;
  left: 0;
  background-color: transparent;
  border: 0;
}

.natural-gallery .natural-gallery-body {
  padding: 0;
  margin: 0;
}

.natural-gallery .natural-gallery-body::after {
  content: '';
  display: block;
  clear: both;
}

.natural-gallery .natural-gallery-body[href] {
  cursor: pointer;
}

.natural-gallery .natural-gallery-body .natural-gallery-noresults {
  margin: 3em auto 2em;
  width: 100px;
  height: 100px;
  text-align: center;
  font-weight: bold;
  font-size: 15px;
  color: #888888;
}

.natural-gallery .natural-gallery-body .natural-gallery-thumbnail img {
  box-shadow: 3px 3px 5px #999999;
  text-align: center;
  will-change: opacity;
}

.natural-gallery .natural-gallery-body .figure {
  position: relative;
  display: block;
  float: left;
  overflow: hidden;
  margin: 0;
}

.natural-gallery .natural-gallery-body .figure:hover .title.hover, .natural-gallery .natural-gallery-body .figure:hover .title.hover {
  transform: translateY(0%);
}

.natural-gallery .natural-gallery-body .figure.loading::before {
  content: "";
  position: absolute;
  z-index: 990;
  top: 50%;
  left: 50%;
  width: 700px;
  height: 700px;
  margin-left: -350px;
  margin-top: -350px;
  border-radius: 50%;
  transform: scale(0, 0);
  opacity: 0.3;
  animation-name: loading;
  animation-duration: 2s;
  animation-delay: 0.2s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-timing-function: ease-out;
  will-change: width, height, opacity;
}

.natural-gallery .natural-gallery-body .figure.loaded.visible > .image {
  opacity: 1;
  transition: 1s ease-out opacity, 1s ease-out transform;
}

.natural-gallery .natural-gallery-body .figure .image {
  display: block;
  position: relative;
  z-index: 100;
  opacity: 0;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.natural-gallery .natural-gallery-body .figure[href] .title {
  cursor: pointer;
  margin: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
  border-radius: 4px;
  text-decoration: none;
}

.natural-gallery .natural-gallery-body .figure .title {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1100;
  background: rgba(0, 0, 0, 0.6);
  text-align: center;
  color: white;
  font-size: 14px;
  line-height: 2em;
  font-weight: normal;
  padding: 10px;
  transition: .2s ease all;
  overflow: hidden;
  white-space: normal;
}

.natural-gallery .natural-gallery-body .figure .title[href]:hover {
  margin-bottom: 12px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.5);
}

.natural-gallery .natural-gallery-body .figure .title.hover {
  padding: 10px;
  transform: translateY(calc(100% + 10px));
}

.natural-gallery .natural-gallery-body .link {
  cursor: pointer;
}

.natural-gallery .natural-gallery-body .button {
  cursor: pointer;
  margin: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
  border-radius: 4px;
  text-decoration: none;
}

.natural-gallery .natural-gallery-body .zoomable {
  cursor: pointer;
}

.natural-gallery .natural-gallery-body .image.zoomable:hover, .natural-gallery .natural-gallery-body .zoomable:hover .image {
  transform: rotate(2deg) scale(1.4);
}

.natural-gallery .natural-gallery-header {
  margin: 0 0 10px 0;
  /* Base for icons */
  /* Section name ======================================= */
  /* active state */
  /* BOTTOM BARS ================================= */
  /* active state */
}

.natural-gallery .natural-gallery-header::after {
  content: '';
  display: block;
  clear: both;
}

.natural-gallery .natural-gallery-header .natural-gallery-searchTerm > svg {
  width: 1.4em;
  height: 1.4em;
  top: 0.4em;
}

.natural-gallery .natural-gallery-header .natural-gallery-searchTerm input {
  border: none;
  background: none;
}

.natural-gallery .natural-gallery-header .natural-gallery-categories {
  /* Hide checkboxes */
}

.natural-gallery .natural-gallery-header .natural-gallery-categories input[type=checkbox] {
  display: none;
}

.natural-gallery .natural-gallery-header .natural-gallery-categories label {
  position: relative;
  cursor: pointer;
}

.natural-gallery .natural-gallery-header .natural-gallery-categories .label {
  margin-left: 1.5em;
  margin-right: 5px;
}

.natural-gallery .natural-gallery-header .sectionContainer {
  margin-top: 30px;
  position: relative;
}

.natural-gallery .natural-gallery-header .natural-gallery-searchTerm,
.natural-gallery .natural-gallery-header .natural-gallery-categories label,
.natural-gallery .natural-gallery-header .natural-gallery-categories {
  float: left;
  margin-right: 5px;
}

.natural-gallery .natural-gallery-header .natural-gallery-searchTerm > svg,
.natural-gallery .natural-gallery-header .natural-gallery-categories .label > svg,
.natural-gallery .natural-gallery-header .natural-gallery-images > svg {
  content: "";
  float: left;
  position: absolute;
  left: 5px;
  font-size: 16px;
  background-repeat: no-repeat;
}

.natural-gallery .natural-gallery-header .natural-gallery-categories .label > svg,
.natural-gallery .natural-gallery-header .natural-gallery-images > svg {
  width: 1.2em;
  height: 1.2em;
  top: 0.5em;
}

.natural-gallery .natural-gallery-header .natural-gallery-searchTerm input,
.natural-gallery .natural-gallery-header .natural-gallery-searchTerm .sectionName,
.natural-gallery .natural-gallery-header .natural-gallery-categories .label,
.natural-gallery .natural-gallery-header .natural-gallery-images {
  display: block;
  padding: 9px 5px 6px;
  font-size: 16px;
  line-height: 1.3em;
  height: 35px;
}

.natural-gallery .natural-gallery-header .natural-gallery-images {
  float: right;
  margin-right: 0;
  margin-left: 5px;
  padding-left: 2em;
}

.natural-gallery .natural-gallery-header .natural-gallery-searchTerm input,
.natural-gallery .natural-gallery-header .natural-gallery-categories .label {
  font-weight: normal;
}

.natural-gallery .natural-gallery-header .natural-gallery-searchTerm input {
  width: 300px;
  padding-left: 2em;
}

.natural-gallery .natural-gallery-header .natural-gallery-searchTerm input:focus {
  outline: none;
}

.natural-gallery .natural-gallery-header .sectionName,
.natural-gallery .natural-gallery-header .natural-gallery-categories label {
  margin-bottom: 0;
}

.natural-gallery .natural-gallery-header .sectionName {
  left: 5px;
  top: 0;
  margin-left: 1.5em;
  font-size: 16px;
  font-weight: normal;
  position: absolute;
  pointer-events: none;
  transition: 0.2s ease all;
}

.natural-gallery .natural-gallery-header .natural-gallery-images .sectionName,
.natural-gallery .natural-gallery-header .natural-gallery-categories .sectionName,
.natural-gallery .natural-gallery-header .natural-gallery-searchTerm:hover .sectionName,
.natural-gallery .natural-gallery-header .natural-gallery-searchTerm input:focus ~ .sectionName,
.natural-gallery .natural-gallery-header .natural-gallery-searchTerm input:valid ~ .sectionName {
  top: -2.2em;
  font-size: 14px !important;
  line-height: 1em;
  padding: 0;
  margin: 1.2em 0 0 0;
}

.natural-gallery .natural-gallery-header .natural-gallery-categories .sectionName:last-child {
  display: none;
}

.natural-gallery .natural-gallery-header .natural-gallery-searchTerm,
.natural-gallery .natural-gallery-header .natural-gallery-categories label,
.natural-gallery .natural-gallery-header .natural-gallery-images {
  border: none;
  border-bottom-width: 1px;
  border-bottom-style: solid;
}

.natural-gallery .natural-gallery-header .bar {
  position: relative;
  display: block;
}

.natural-gallery .natural-gallery-header .bar::before,
.natural-gallery .natural-gallery-header .bar::after {
  content: '';
  height: 3px;
  width: 0;
  bottom: -2px;
  position: absolute;
  transition: 0.2s ease all;
}

.natural-gallery .natural-gallery-header .bar::before {
  left: 50%;
}

.natural-gallery .natural-gallery-header .bar::after {
  right: 50%;
}

.natural-gallery .natural-gallery-header .natural-gallery-searchTerm input:focus ~ .bar::before,
.natural-gallery .natural-gallery-header .natural-gallery-searchTerm input:focus ~ .bar::after,
.natural-gallery .natural-gallery-header .natural-gallery-searchTerm input:valid ~ .bar::before,
.natural-gallery .natural-gallery-header .natural-gallery-searchTerm input:valid ~ .bar::after,
.natural-gallery .natural-gallery-header .natural-gallery-categories input[type=checkbox]:checked ~ .bar::before,
.natural-gallery .natural-gallery-header .natural-gallery-categories input[type=checkbox]:checked ~ .bar::after {
  width: 50%;
}

.natural-gallery .natural-gallery-next {
  display: block;
  margin: 10px auto;
  text-align: center;
  height: 30px;
}

.natural-gallery .natural-gallery-next svg {
  cursor: pointer;
  font-size: 16px;
  height: inherit;
}

.natural-gallery .natural-gallery-next::after {
  content: '';
  display: block;
  clear: both;
}

@keyframes loading {
  0% {
    transform: scale(0, 0);
    opacity: 0.3;
  }
  100% {
    transform: scale(1, 1);
    opacity: 0.05;
  }
}



